*{
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
}


.hide_cover{
  background: rgb(245, 245, 245);
  font-size: 14px;
  // height: 100vh;
  line-height: 1.5;
  min-height: 100vh;

  .alert{
    width: 500px;
    position: absolute;
    top: 0;
    left:30%;
    z-index: 999;
  }
  .alerts{
    position: absolute;
    top: 14px;
    left: 64%;
    z-index: 999;
}
}
// 顶部倒计时部分
#pageTimer, #timeCounter {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 100;
  color: #707070;
  font-size: 13px;
  height: 44px;
  line-height: 44px;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 10%);
}
#timeCounter {
  .icon_time {
    display: inline-block;
    height: 18px;
    width: 18px;
    background: url(https://s1.wenjuan.com/static/images/wjw-page-icon.png?v=20190903) no-repeat;
    background-size: 808px auto;
    vertical-align: middle;
    background-position: -620px -556px;
    margin-top: -2px;
    margin-right: 3px;
  }
  #submitTimer {
    display: inline-block;
  }
}
// 底部答题卡部分
.wjContent {
  padding-top: 30px;
  background-color: rgba(255,255,255,1);
  word-break: break-all;
  width: 814px;
  margin: 0 auto 10px;
  min-height: 200px;
  background: #fff;
  word-break: normal;
  z-index: 99;
  position: relative;
  // 第一个大盒子
  .content_box.content_top {
    padding-top: 30px;
    // 前端头部
    #begin_content {
      clear: both;
      overflow: hidden;
      // 前端面试题
      .wjtitle {
        h1{
          text-align: center;
          color: #4c4c4c;
          font-size: 18px;
          font-family: inherit;
          font-weight: 400;
          margin: 0;
          text-rendering: optimizelegibility;
          
        }
        p {
          display: block;
          margin-block-start: 1em;
          margin-block-end: 1em;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
        }
      }
      // 感谢
      .wjintro {
        font-size: 16px;
        float: left;
        width: 100%;
      }
      .mtop {
        margin-top: 20px;
      }
      .desc_begin:after{
        content: ".";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
      }
      // 蓝色的线
      .wjhr {
        background-color: #53a4f4!important;
        float: left;
        width: 100%;
        height: 3px;
        background-color: #f29400;
      }
      .mtop{
        margin-top: 20px;
      }
    }
    // 单选多选题
    #question_box {
      width: 100%;
      .wjques{
        padding-left: 15px;
      }
      .maxtop{
        width: auto;
        margin-top: 30px;
      }
      // 姓名
      .title{
        width: 100%;
        font-size: 16px;
        margin-left: -5px;
        padding-bottom: 8px;
        position: relative;
        padding-left: 5px;
        .asterisk {
          color: #ff6d56;
          margin-left: 5px;
          font-size: 16px;
        }
      }
      // 姓名下的input框
      .matrix {
        overflow-x: auto;
        overflow-y: hidden;
        width: 330px;
        margin-left: -7px;
        padding-left: 7px;
      }
    }
  }
  .content_box {
    padding: 10px 40px 66px;
    
  }
  .btn{
    margin-left: 317px;
    margin-top: 40px;
  }
  .btns{
    display: none;
  }
}
.clear:after {
  clear: both;
  content: ".";
  display: block;
  height: 0;
  visibility: hidden;
}
